 /* //////////////////////////////////////////////////////////////////////////
 
    III. Theme
 
    ////////////////////////////////////////////////////////////////////////// */
 
 /* --------------------------------------------------------------------------
    1.Global
    -------------------------------------------------------------------------- */
 
 /* Base Styles
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 html,
 body {
    height: 100%;
    max-height: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
 }
 
 body {
    font-family: "HCo Whitney SSm", "Roboto", "Helvetica", sans-serif;
    font-size: 18px;
    font-weight: var(--font-weight-primary-regular);
    line-height: 1.5;
    word-wrap: break-word;
    word-break: break-word;
    color: var(--color-font);
    background-color: var(--color-body);
 }
 
 /* Typography
    –––––––––––––––––––––––––––––––––––––––––––––––––– */
 h1,h2,h3,h4,h5,h6 {
    font-weight: var(--font-weight-primary-bold);
    line-height: 1.4;
    margin: 5px 0 25px;
 }
 
 h1 { font-size: 50px }
 h2 { font-size: 40px }
 h3 { font-size: 30px }
 h4 { font-size: 25px }
 h5 { font-size: 20px }
 h6 { font-size: 17px }
 
 /* Global Links
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 a,
 a:hover,
 a:focus {
    text-decoration: none;
    color: var(--color-font);
    outline: 0;
 }
 
 /* Global Flexbox
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .flex {
    display: flex;
    flex-wrap: wrap;
 }
 
 /* Global Tags
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .global-tag {
    font-size: 10px;
    font-weight: var(--font-weight-primary-bold);
    display: inline-block;
    margin: 0 2px 1px 0;
    padding: 4px 10px;
    transition: all .15s ease;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    border-radius: 20px;
 }
 
 /* Global Button
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .global-button {
    font-size: 14px;
    font-weight: var(--font-weight-primary-bold);
    line-height: 1.15;
    display: inline-block;
    box-sizing: border-box;
    height: auto;
    margin: 5px 0 0 0;
    padding: 25px 27px;
    cursor: pointer;
    transition: all .15s ease;
    text-decoration: none;
    letter-spacing: 2px;
    color: var(--color-font-button);
    border: none !important;
    border-radius: 50px;
    outline: 0;
    background-color: var(--color-elements-two);
 }
 
 br + .global-button {
    margin-top: 25px;
 }
 
 .global-button:hover {
    color: var(--color-elements-white);
    border-color: var(--ghost-accent-color);
    background-color: var(--ghost-accent-color);
 }
 
 /* Members — Notifications
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .global-notification div {
    font-size: 15px;
    font-weight: var(--font-weight-primary-bold);
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    left: 0;
    display: none;
    visibility: hidden;
    padding: 12px 20px;
    transform: translateY(-130%);
    -webkit-animation: slideDownNotification 5s cubic-bezier(.19, 1, .22, 1) forwards;
    animation: slideDownNotification 5s cubic-bezier(.19, 1, .22, 1) forwards;
    text-align: center;
    color: var(--color-elements-white);
    background-color: var(--color-bg-notification-success);
 }
 
 .global-notification .expired {
    background-color: var(--color-bg-notification-false);
 }
 
 .global-notification.is-subscribe .subscribe,
 .global-notification.is-signin .signin,
 .global-notification.is-signup .signup,
 .global-notification.is-expired .expired,
 .global-notification.is-checkout-success .checkout-success {
    display: block;
 }
 
 @-webkit-keyframes slideDownNotification {
    from, to { visibility: visible }
    15% { transform: translateY(0) }
    85% { transform: translateY(0) }
 }
 
 @keyframes slideDownNotification {
    from, to { visibility: visible }
    15% { transform: translateY(0) }
    85% { transform: translateY(0) }
 }
 
 /* Sticky Footer
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .global-wrap {
    display: flex;
    flex-direction: column;
    height: 100%;
 }
 
 .section-content-wrap {
    flex: 1 0 auto;
 }
 
 .section-footer {
    flex-shrink: 0;
 }
 
 /* Wrap
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .wrap {
    max-width: 1200px;
    margin: 0 auto;
 }
 
 /* RWD — Global
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 @media (max-width:479px) {
    h1,h2,h3,h4,h5,h6 { margin: 3px 0 20px }
    h1 { font-size: 37px }
    h2 { font-size: 31px }
    h3 { font-size: 25px }
    h4 { font-size: 20px }
    h5 { font-size: 15px }
    h6 { font-size: 12px }
 
    .global-button {
       padding: 20px 23px;
    }
 
    .wrap {
       width: 100%;
       margin: 0;
    }
 
 }
 
 @media (min-width:480px) and (max-width:767px) {
    h1,h2,h3,h4,h5,h6 { margin: 3px 0 20px }
    h1 { font-size: 45px }
    h2 { font-size: 35px }
    h3 { font-size: 27px }
    h4 { font-size: 22px }
    h5 { font-size: 17px }
    h6 { font-size: 14px }
 }
 
 @media (min-width:480px) and (max-width:1399px) {
    .wrap {
       width: calc(100% - 85px - 85px);
    }
 
 }
 
 /* Fixed sub-pixel rendering */
 @media (min-width:1025px) {
    .item-wrap article,
    .author-wrap,
    .post-wrap .kg-bookmark-container,
    .page-author-wrap,
    .page-tag-wrap,
    .membership-card {
       will-change: transform;
    }
 
    /* Safari */
    _:lang(x)+_:-webkit-full-screen-document,
    .item-wrap article,
    .author-wrap,
    .post-wrap .kg-bookmark-container,
    .page-author-wrap,
    .page-tag-wrap,
    .membership-card {
       will-change: auto;
    }
 
 }
 
 @media (min-width:1921px) {
    .global-wrap {
       max-width: 1920px;
       margin: 0 auto;
    }
 
    .search-opened .global-wrap {
       position: initial !important;
    }
 
 }
 
 /* --------------------------------------------------------------------------
    2.Header
    -------------------------------------------------------------------------- */
 .header-wrap {
    align-items: center;
    flex-wrap: nowrap;
    min-height: 160px;
    padding: 0;
 }
 
 .header-logo {
    line-height: initial;
    flex: 0 1 250px;
    box-sizing: border-box;
    padding: 0 20px 0 0;
 }
 
 .header-nav {
    flex: 0 0 calc(100% - 250px);
    text-align: right;
 }
 
 .header-logo h1 {
    font-size: initial;
    line-height: initial;
    margin: initial;
 }
 
 .header-logo .is-logo {
    line-height: 0;
 }
 
 .header-logo .is-logo img {
    max-width: 100%;
    max-height: 50px;
 }
 
 .header-logo .no-logo {
    font-size: 40px;
    font-weight: var(--font-weight-primary-bold);
    line-height: .9;
    margin: 0;
    padding: 0;
 }
 
 /* Description
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .blog-description {
    font-size: 13px;
    font-weight: var(--font-weight-primary-bold);
    position: fixed;
    z-index: 999;
    top: 0;
    bottom: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    max-width: 90vh;
    height: 86px;
    margin: auto;
    transform: rotate(-90deg) translate(-50%, 50%);
    transform-origin: 0 50%;
    text-align: center;
    letter-spacing: 2.5px;
    text-transform: uppercase;
    color: var(--color-font);
 }
 
 /* RWD — Header
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 @media (max-width:479px) {
    .section-header {
       width: calc(100% - 30px - 30px);
       margin: 0 auto;
    }
 
    .header-wrap {
       min-height: 100px;
    }
 
    .header-logo .is-logo img {
       max-height: 40px;
    }
 
    .header-logo .no-logo {
       font-size: 25px;
    }
 
    .blog-description {
       visibility: hidden;
    }
 
 }
 
 /* --------------------------------------------------------------------------
    3.Navigation
    -------------------------------------------------------------------------- */
 .nav-wrap {
    font-size: 15px;
    font-weight: var(--font-weight-primary-bold);
    position: relative;
    width: auto;
    padding: 0;
    letter-spacing: .9px;
 }
 
 .nav-wrap > .nav-label {
    display: none;
 }
 
 .nav-wrap > .nav-list {
    display: block;
 }
 
 .nav-wrap > .nav-list > .nav-list-item {
    position: relative;
    display: inline-block;
    background-color: transparent;
 }
 
 .nav-wrap input[type='checkbox'] {
    display: none;
 }
 
 .nav-list {
    z-index: 999;
    min-width: 150px;
    margin: 0;
    padding: 0;
    list-style: none;
 }
 
 .nav-list,
 [id^='toggle'] {
    display: none;
 }
 
 .nav-list-item {
    margin: 0;
 }
 
 .nav-list-item,
 [id^='toggle']:checked + .nav-list {
    position: absolute;
    right: 0;
    display: block;
    border-radius: var(--border-radius-primary);
    background-color: var(--color-bg-elements);
 }
 
 .nav-link {
    line-height: .5;
    display: block;
    margin: 0;
    padding: 10px;
    text-decoration: none;
 }
 
 /* Dot
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .nav-dot {
    position: absolute;
    top: 1px;
    right: 2px;
    width: 7px;
    height: 7px;
    transition: all .25s ease;
    border-radius: 50%;
 }
 
 .nav-dot.nav-dot-current,
 .nav-link:active ~ .nav-dot {
    background-color: var(--ghost-accent-color) !important;
 }
 
 .nav-link:hover ~ .nav-dot {
    background-color: var(--color-hover-dot);
 }
 
 /* RWD — Navigation
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 @media (max-width:479px) {
    .nav-list {
       margin: 20px -15px 0 0;
    }
 
 }
 
 @media (min-width:480px) and (max-width:1023px) {
    .nav-list {
       margin: 20px 0 0 0;
    }
 
 }
 
 @media (max-width:1023px) {
    .nav-list {
       min-width: 170px;
       padding: 20px 20px 30px 0;
       word-break: initial;
    }
 
    .nav-wrap > .nav-label {
       display: block;
    }
 
    .nav-wrap > .nav-list {
       display: none;
    }
 
    .nav-wrap > .nav-list > .nav-list-item {
       position: relative;
       display: block;
    }
 
    .nav-link {
       line-height: 1;
       padding: 10px 10px 10px 30px;
    }
 
    .header-wrap .nav-dot {
       top: 4px;
    }
 
 }
 
 @media (min-width:1024px) and (max-width:1199px) {
    .nav-wrap {
       font-size: 13px;
    }
 
    .nav-link {
       padding: 8px;
    }
 
    .nav-wrap .nav-dot {
       top: -1px;
       right: 0;
    }
 
 }
 
 /* RWD — "Three" dots navigation
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 @media (max-width:1023px) {
    .nav-dots,
    .nav-dots-wrap {
       margin: 0;
       padding: 0;
    }
 
    .nav-dots-wrap .nav-list-item {
       font-size: 15px;
       position: relative;
       letter-spacing: .9px;
    }
 
 }
 
 @media (min-width:1024px) {
    .nav-dots {
       display: inline-block;
       box-sizing: border-box;
       margin: 0;
       padding: 0 7px 0 0;
       cursor: pointer;
       -webkit-user-select: none;
       -moz-user-select: none;
       -ms-user-select: none;
       user-select: none;
       pointer-events: none;
    }
 
    .nav-dots::before {
       display: block;
       width: 35px;
       content: '•••';
       letter-spacing: 3px;
       pointer-events: auto;
    }
 
    .nav-dots .nav-dots-wrap {
       font-size: 15px;
       font-weight: var(--font-weight-primary-bold);
       position: absolute;
       z-index: 999;
       top: 40px;
       display: none;
       min-width: 140px;
       margin: 0;
       padding: 20px 10px 15px;
       list-style: none;
       transform: translateX(calc(-100% + 50px));
       word-break: normal;
       border-radius: var(--border-radius-secondary);
       background-color: var(--color-bg-elements);
    }
 
    .nav-dots.is-active .nav-dots-wrap {
       display: block;
    }
 
    .nav-dots .nav-list-item {
       position: relative;
