 @media (min-width:1200px) and (max-width:1399px) {
    .featured-wrap h1,
    .featured-wrap h2 {
       font-size: 45px;
    }
 
 }
 
 @media (min-width:1400px) {
    .tag-hash-full-image .featured-image,
    .tag-hash-post-violet .no-featured-image,
    .tag-hash-post-green .no-featured-image,
    .tag-hash-post-red .no-featured-image,
    .tag-hash-post-orange .no-featured-image,
    .tag-hash-post-yellow .no-featured-image,
    .tag-hash-post-blue .no-featured-image {
       left: 85px;
       overflow: hidden;
       width: calc(100% - 170px);
       border-radius: var(--border-radius-secondary);
    }
 
 }
 
 @media (min-width:2000px) {
    .featured-wrap {
       min-height: 900px
    }
 
 }
 
 /* --------------------------------------------------------------------------
    6.Loop
    -------------------------------------------------------------------------- */
 .section-loop {
    position: relative;
    margin-top: 0;
 }
 
 .section-loop.is-featured {
    margin-top: -10vh;
 }
 
 .section-loop.no-featured {
    margin-top: 10vh;
 }
 
 .items-wrap {
    margin: 0 -15px;
    -webkit-animation: slideTop .7s cubic-bezier(.250, .460, .450, .940) both;
    animation: slideTop .7s cubic-bezier(.250, .460, .450, .940) both;
 }
 
 @-webkit-keyframes slideTop {
    0% { transform: translateY(100px) }
    100% { transform: translateY(0) }
 }
 
 @keyframes slideTop {
    0% { transform: translateY(100px) }
    100% { transform: translateY(0) }
 }
 
 .item-wrap article {
    position: relative;
    z-index: 0;
    overflow: hidden;
    box-sizing: border-box;
    width: calc(100% - 30px);
    min-height: 370px;
    margin: 15px 0 15px 15px;
    padding: 40px 38px 45px;
    transition: transform .2s ease;
    border-radius: var(--border-radius-primary);
    background-image: linear-gradient(20deg, var(--gradient-gray-start), var(--gradient-gray-end));
 }
 
 .item-wrap.is-image article {
    background-color: transparent;
    background-image: initial;
 }
 
 .item-wrap article:hover {
    transform: translateY(-4px);
 }
 
 .item-link-overlay {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius-primary);
 }
 
 /* Flexbox
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .item-wrap {
    flex: 999 0 33.333%;
    min-width: 350px;
 }
 
 .item-wrap.tag-hash-large {
    flex: 1 0 66.666%;
 }
 
 /* Title
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .item-wrap h2 {
    font-size: 33px;
    font-weight: var(--font-weight-primary-bold);
    line-height: 1.35;
    margin-right: 10%;
 }
 
 /* Images
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .item-image {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: no-repeat center center;
    background-size: cover;
 }
 
 .item-image::before {
    display: block;
    width: 100%;
    height: 100%;
    content: '';
    opacity: .4;
    background-image: linear-gradient(234deg, #394245, #000);
 }
 
 /* Images - Link - Background colors
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .section-loop .is-image a,
 .section-loop .tag-hash-violet a,
 .section-loop .tag-hash-green a,
 .section-loop .tag-hash-red a,
 .section-loop .tag-hash-orange a,
 .section-loop .tag-hash-yellow a,
 .section-loop .tag-hash-blue a {
    color: var(--color-elements-white);
 }
 
 /* Images - Text - Background colors & image
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .section-loop .is-image .white,
 .section-loop .tag-hash-violet .white,
 .section-loop .tag-hash-green .white,
 .section-loop .tag-hash-red .white,
 .section-loop .tag-hash-orange .white,
 .section-loop .tag-hash-yellow .white,
 .section-loop .tag-hash-blue .white {
    color: var(--color-elements-white);
 }
 
 /* Images - Opacity image - Background colors
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .section-loop .tag-hash-violet .item-image::before,
 .section-loop .tag-hash-green .item-image::before,
 .section-loop .tag-hash-red .item-image::before,
 .section-loop .tag-hash-orange .item-image::before,
 .section-loop .tag-hash-yellow .item-image::before,
 .section-loop .tag-hash-blue .item-image::before {
    opacity: .8;
 }
 
 /* Meta
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .item-meta {
    line-height: 1.3;
 }
 
 .item-meta span,
 .item-meta time {
    font-size: 14px;
    opacity: .9;
 }
 
 .item-meta time {
    display: inline-block;
    margin-right: 15px;
 }
 
 .item-meta a {
    font-size: 16px;
    font-weight: var(--font-weight-primary-bold);
    position: relative;
    z-index: 2;
 }
 
 /* Primary tag
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .is-primary-tag {
    margin-bottom: 60px;
 }
 
 .primary-tag {
    position: absolute;
    z-index: 2;
    bottom: 40px;
    margin-left: -8px;
    padding: 4px 8px;
    background-color: transparent;
 }
 
 .primary-tag:hover {
    color: var(--color-font) !important;
    background-color: var(--color-body);
 }
 
 /* Load more & Pagination
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .pagination {
    display: none;
 }
 
 .load-more,
 .section-load-more {
    width: 40px;
    height: 40px;
 }
 
 .section-load-more {
    margin: 20px auto 16vh;
 }
 
 .load-more {
    display: none;
    box-sizing: border-box;
    cursor: pointer;
    transition: all .3s ease;
    border: 8px solid var(--color-elements-two);
    border-radius: 100px;
    outline: none;
    background: none;
 }
 
 .load-more span {
    display: none;
 }
 
 .load-more:hover {
    transition: .3s cubic-bezier(.39, .07, .68, 1.7);
    transform: scale(.65);
    border-color: var(--ghost-accent-color);
    background-color: var(--ghost-accent-color) !important;
 }
 
 /* Load more with text
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .global-hash-load-more .load-more,
 .global-hash-load-more .section-load-more {
    width: auto;
    text-align: center;
 }
 
 .global-hash-load-more .load-more {
    height: auto;
    margin: 5px 0 0 0;
    cursor: pointer;
    transition: all .15s ease;
    text-decoration: none;
    color: initial;
    color: var(--color-font-button);
    border: none;
    border-radius: 50px;
    outline: 0;
    background-color: var(--color-elements-two);
 }
 
 .global-hash-load-more .load-more span {
    font-size: 14px;
    font-weight: var(--font-weight-primary-bold);
    display: inline-block;
    padding: 25px 27px;
    letter-spacing: 2px;
 }
 
 .global-hash-load-more .load-more:hover {
    transform: none;
    color: var(--color-elements-white);
 }
 
 /* RWD — Loop
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 @media (max-width:479px) {
    .items-wrap {
       margin: 0;
    }
 
    .item-wrap {
       min-width: 280px;
    }
 
    .item-wrap article {
       min-height: 200px;
       margin: 0 15px 15px;
       padding: 30px 30px 40px;
    }
 
    .item-wrap h2 {
       font-size: 25px;
       margin-right: 0;
    }
 
    .section-load-more {
       margin-bottom: 7vh;
    }
 
    .item-meta a {
       font-size: 14px;
    }
 
    .global-hash-load-more .section-load-more {
       padding-bottom: 25px;
    }
 
    .global-hash-load-more .load-more span {
       padding: 18px 22px;
    }
 
 }
 
 @media (min-width:480px) and (max-width:767px) {
    .item-wrap article {
       min-height: 270px;
    }
 
    .item-wrap h2 {
       font-size: 28px;
       margin-right: 0;
    }
 
    .section-load-more {
       margin-bottom: 8vh;
    }
 
    .item-meta a {
       font-size: 14px;
    }
 
 }
 
 @media (min-width:768px) and (max-width:1023px) {
    .section-load-more {
       margin-bottom: 10vh;
    }
 
    .item-meta a {
       font-size: 15px;
    }
 
 }
 
 /* --------------------------------------------------------------------------
    7.Post
    -------------------------------------------------------------------------- */
 .section-post {
    position: relative;
    margin: calc(-10vh + 15px) auto 0;
    border-radius: var(--border-radius-primary);
    background-color: var(--color-body);
 }
 
 .post-wrap {
    font-size: 19px;
    overflow: visible;
    max-width: 700px;
    margin: 0 auto;
    /* padding: 60px 85px 120px; */
 }
 
 .post-wrap.no-image {
    padding-top: 0;
 }
 
 .post-wrap a {
    transition: all .2s ease;
    border-bottom: 2px solid var(--color-underline-link);
 }
 
 .post-wrap a:hover {
    border-color: var(--ghost-accent-color);
 }
 
 .post-meta {
    position: relative;
    max-width: 263px;
    margin: 0 auto;
 }
 
 /* Share
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .post-share {
    position: absolute;
    z-index: 99;
    bottom: -30px;
    display: inline-flex;
    align-items: center;
    margin: 50px auto 0;
    padding: 11px 13px;
    border-radius: 26px;
    background-color: var(--color-body);
 }
 
 .post-share a {
    line-height: 0;
    padding: 11px 30px 9px;
    cursor: pointer;
    border: none;
 }
 
 .post-share svg {
    width: 18px;
    height: 18px;
    transition: all .2s ease;
    fill: var(--color-font);
 }
 
 .post-share svg:hover {
    fill: var(--ghost-accent-color);
 }
 
 .post-share .copy {
    padding: 8px 28px 6px 27px;
 }
 
 .post-share .copy svg {
    width: 23px;
    height: 24px;
 }
 
 .post-share input {
    position: absolute;
    top: -24px;
    display: block;
    cursor: default;
    opacity: 0;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
 }
 
 .post-share .copy-popup {
    font-size: 12px;
    font-weight: var(--font-weight-primary-bold);
    position: absolute;
    top: -22px;
    left: -17px;
    width: 300px;
    margin-bottom: 50px;
    text-align: center;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    opacity: 0;
 }
 
 .post-share .copy:not(:active) ~ .copy-popup {
    transition: opacity 2s step-end;
 }
 
 .post-share .copy:active ~ .copy-popup {
    opacity: .8;
 }
 
 /* Footnotes
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .footnotes-sep {
    width: 100px;
    margin: 50px 0 10px;
 }
 
 .footnotes {
    padding: 20px 0;
 }
 
 .footnotes-list {
    padding-left: 20px;
    list-style: decimal;
 }
 
 .footnotes p,
 .footnote-item {
    font-size: 14px;
    line-height: 1.3;
    margin-bottom: 10px;
 }
 
 /* Disable styling for img when is a link
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .post-wrap .image-link {
    border: none;
 }
 
 /* Paragraph
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 p {
    line-height: 1.65;
 }
 
 /* Image
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 p img {
    width: calc(100% + 110px);
    height: auto;
    margin: 35px 0 35px -55px;
    border-radius: var(--border-radius-secondary);
 }
 
 /* Unsplash Image <small>
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 p img ~ small {
    font-size: 12px;
    line-height: 1.1;
    position: absolute;
    display: block;
    max-width: 300px;
    transform: rotate(-90deg);
    transform-origin: -64px 20px;
 }
 
 p img ~ small,
 p img ~ small a {
    color: var(--color-figcaption);
 }
 
 /* Blockquote
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 blockquote {
    font-size: 28px;
    font-weight: var(--font-weight-primary-bold);
    line-height: 1.4;
    width: 90%;
    margin: 5vh 0 5vh -55px;
    padding: 20px 50px;
    border-left: 5px solid var(--color-elements-one);
 }
 
 blockquote p {
    margin: 0;
 }
 
 /* Code
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .kg-code-card {
    min-width: 100%;
    margin: 30px 0 15px;
 }
 
 .kg-code-card pre {
    margin: 0 0 10px;
 }
 
 .kg-code-card figcaption {
    font-size: 12px;
    line-height: 1.3;
    display: block;
    width: 100%;
    margin-top: 5px;
    color: var(--color-figcaption);
 }
 
 code {
    font-size: 90%;
    margin: 0;
    padding: 2px 5px;
    border-radius: var(--border-radius-secondary);
    background-color: var(--color-bg-code);
 }
 
 pre > code {
    display: block;
    padding: 10px 15px;
    white-space: pre-wrap;
 }
 
 pre {
    margin-top: 30px;
 }
 
 /* Lists
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .post-wrap ul,
 .post-wrap ol {
    margin-left: 1em;
 }
 
 .post-wrap ul {
    list-style: disc outside;
 }
 
 .post-wrap ul li ul {
    list-style: circle outside;
 }
 
 .post-wrap ol,
 .post-wrap ol li ol {
    list-style: decimal outside;
 }
 
 dl dt {
    font-weight: var(--font-weight-primary-bold);
    float: left;
    clear: left;
    overflow: hidden;
    width: 180px;
    margin-bottom: 1em;
    text-align: right;
    white-space: nowrap;
    text-overflow: ellipsis;
 }
 
 dl dd {
    margin-bottom: 1em;
    margin-left: 200px;
 }
 
 ul {
    list-style: disc inside;
 }
 
 ol {
    list-style: decimal inside;
 }
 
 ol,
 ul {
    margin: 0 0 25px;
    padding-left: 20px;
 }
 
 ul ul,
 ul ol,
 ol ol,
 ol ul {
    font-size: 90%;
    margin: 15px 0 15px 30px;
 }
 
 li {
    margin-bottom: 10px;
 }
 
 /* Tables
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 table {
    box-sizing: border-box;
    width: 100%;
    max-width: 100%;
    margin: 17px 0;
    border-collapse: collapse;
    background-color: transparent;
 }
 
 th,
 td {
    padding: 10px 12px;
    text-align: left;
    border-bottom: 1px solid var(--color-table-border);
 }
 
 th:first-child,
 td:first-child {
    padding-left: 0;
 }
 
 th:last-child,
 td:last-child {
    padding-right: 0;
 }
 
 /* Responsive */
 .responsive-table {
    overflow-x: auto;
    word-break: normal;
 }
 
 /* Elements
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 hr {
    width: 100%;
    height: 5px;
    margin: 40px 0 25px;
    border: none;
    background-color: var(--color-elements-one);
 }
 
 mark {
    background-color: yellow;
 }
 
 kbd {
    font-size: 14px;
    font-weight: var(--font-weight-primary-bold);
    display: inline-block;
    margin-bottom: 9px;
    padding: 1px 8px;
    color: #666;
    border: #ccc 1px solid;
    border-radius: var(--border-radius-secondary);
    background-color: #f4f4f4;
    box-shadow: 0 1px 0 rgba(0, 0, 0, .2), 0 1px 0 0 var(--color-elements-white) inset;
    text-shadow: var(--color-elements-white) 0 1px 0;
 }
 
 iframe {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
 }
 
 /* Center social elements e.g. Twitter, Facebook, etc
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 iframe,
 .kg-embed-card > div,
 .kg-embed-card > iframe,
 .kg-embed-card > .fb-post,
 .kg-embed-card > .twitter-tweet {
    margin-right: auto !important;
    margin-left: auto !important;
 }
 
 /* Koenig editor images
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .kg-image-card,
 .kg-gallery-card,
 .kg-embed-card {
    margin: 50px 0;
 }
 
 .kg-image-card img,
 .kg-gallery-card img {
    width: 100%;
    height: auto;
    border-radius: var(--border-radius-secondary);
 }
 
 .kg-image-card figcaption,
 .kg-embed-card figcaption,
 .kg-gallery-card figcaption {
    font-size: 12px;
    line-height: 1.3;
    display: block;
    width: 100%;
    margin-top: 5px;
    color: var(--color-figcaption);
 }
 
 .kg-gallery-card .kg-gallery-container {
    display: flex;
    flex-direction: column;
    margin: 15px auto;
 }
 
 .kg-gallery-card .kg-gallery-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
 }
 
 .kg-gallery-card .kg-gallery-image img {
    display: block;
    margin: 0;
 }
 
 .kg-gallery-card .kg-gallery-row:not(:first-of-type) {
    margin: 15px 0 0 0;
 }
 
 .kg-gallery-card .kg-gallery-image:not(:first-of-type) {
    margin: 0 0 0 15px;
 }
 
 /* Bookmark card
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .kg-bookmark-card {
    margin: 50px 0;
 }
 
 .kg-bookmark-card + .kg-bookmark-card {
    margin-top: -10px;
 }
 
 .kg-bookmark-card .kg-bookmark-container {
    overflow: hidden;
    min-height: 140px;
    transition: transform .15s ease-in-out;
    text-decoration: none;
    border-bottom: none;
    border-radius: var(--border-radius-secondary);
    background-color: var(--color-bg-elements);
    background-image: none;
 }
 
 .kg-bookmark-container:hover {
    transform: translateY(-2px);
 }
 
 .kg-bookmark-container,
 .kg-bookmark-content,
 .kg-bookmark-metadata {
    display: flex;
 }
 
 .kg-bookmark-title,
 .kg-bookmark-metadata {
    font-weight: var(--font-weight-primary-bold);
 }
 
 .kg-bookmark-description,
 .kg-bookmark-metadata {
    display: -webkit-box;
    overflow-y: hidden;
    -webkit-box-orient: vertical;
 }
 
 /* Content */
 .kg-bookmark-content {
    align-items: flex-start;
    flex-direction: column;
    flex-grow: 1;
    justify-content: start;
    padding: 20px;
 }
 
 /* Title */
 .kg-bookmark-title {
    font-size: 20px;
    line-height: 1.3;
 }
 
 /* Description */
 .kg-bookmark-description {
    font-size: 15px;
    line-height: 1.3;
    max-height: 40px;
    margin-top: 12px;
    -webkit-line-clamp: 2;
 }
 
 /* Meta */
 .kg-bookmark-metadata {
    font-size: 14px;
    align-items: center;
    flex-wrap: wrap;
    height: 22px;
    margin-top: 18px;
    -webkit-line-clamp: 1;
 }
 
 .kg-bookmark-metadata .kg-bookmark-icon {
    width: 20px;
    height: 20px;
    margin-right: 8px;
    margin-bottom: -5px;
 }
 
 .kg-bookmark-author,
 .kg-bookmark-publisher {
    height: 20px;
 }
 
 .kg-bookmark-metadata span:nth-of-type(2)::before {
    margin-right: 6px;
    margin-left: 6px;
    content: '•';
 }
 
 /* Image */
 .kg-bookmark-thumbnail {
    position: relative;
    min-width: 30%;
    max-height: 100%;
 }
 
 .kg-bookmark-thumbnail img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
 }
 
 /* figcaption */
 .kg-bookmark-card figcaption {
    font-size: 12px;
    line-height: 1.3;
    display: block;
    width: 100%;
    margin-top: 10px;
    color: var(--color-figcaption);
 }
 
 /* RWD — Post
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 @media (max-width:479px) {
    .section-post {
       margin-top: -10vh;
    }
 
    .post-wrap {
       font-size: 16px;
       padding: 25px 10% 100px 25px;
    }
 
    .post-share {
       bottom: 15px;
    }
 
    p img {
       width: 100%;
       margin: 35px auto;
    }
 
    blockquote {
       font-size: 20px;
    }
 
    .kg-bookmark-container {
       flex-direction: column;
    }
 
    .kg-bookmark-content {
       order: 2;
    }
 
    .kg-bookmark-thumbnail {
       order: 1;
       width: 100%;
       min-height: 160px;
    }
 
 }
 
 @media (max-width:767px) {
    .section-post {
       width: calc(100% - 15px - 15px);
    }
 
    p img ~ small {
       line-height: 1.3;
       position: initial;
       margin: -20px 0 20px;
       transform: initial;
       transform-origin: initial;
    }
 
