    -------------------------------------------------------------------------- */
 .section-subscribe {
    margin-bottom: 10vh;
 }
 
 .post-template .section-subscribe {
    margin-top: 15vh;
 }
 
 .page-template .section-subscribe {
    margin-top: 5vh;
 }
 
 .subscribe-wrap {
    padding: 0 20px;
    text-align: center;
 }
 
 .subscribe-wrap h3 {
    font-size: 48px;
    line-height: 1.2;
    max-width: 700px;
    margin: 5px auto 20px;
    padding: 10px 0;
    letter-spacing: 1px;
 }
 
 .subscribe-wrap input {
    border: none;
    border-radius: 0;
    outline: 0;
    box-shadow: none;
 }
 
 .subscribe-wrap .subscribe-input {
    font-size: 20px;
    font-weight: var(--font-weight-primary-bold);
    width: 80%;
    max-width: 500px;
    height: 35px;
    margin: 4vh 0;
    padding: 6px 9px;
    transition: all .25s ease;
    text-align: center;
    word-break: normal;
    color: var(--color-font);
    border-bottom: 7px solid var(--color-elements-two);
    background: transparent;
 }
 
 .subscribe-wrap .subscribe-input.name {
    margin-bottom: 0;
 }
 
 .subscribe-wrap .subscribe-input:focus {
    border-bottom-color: var(--ghost-accent-color) !important;
 }
 
 .subscribe-input::-webkit-input-placeholder {
    color: var(--color-elements-two);
 }
 
 .subscribe-input::-ms-input-placeholder {
    color: var(--color-elements-two);
 }
 
 .subscribe-input::-moz-placeholder {
    opacity: 1;
    color: var(--color-elements-two);
 }
 
 .subscribe-input::placeholder {
    color: var(--color-elements-two);
 }
 
 /* iTyped
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .subscribe-wrap .ityped {
    font-size: 24px;
    font-weight: var(--font-weight-primary-bold);
    line-height: 1.3;
    max-width: 450px;
    margin: 0 auto;
 }
 
 .subscribe-wrap .ityped-cursor {
    transition: color .6s ease-in-out;
    -webkit-animation: .6s blink infinite;
    animation: .6s blink infinite;
 }
 
 @-webkit-keyframes blink {
    from, to { color: transparent }
    50% { color: var(--ghost-accent-color) }
 }
 
 @keyframes blink {
    from, to { color: transparent }
    50% { color: var(--ghost-accent-color) }
 }
 
 /* Alerts
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .subscribe-alert-loading,
 .subscribe-alert-error {
    display: none;
 }
 
 .subscribe-form.loading ~ .subscribe-alert-loading,
 .subscribe-form.error ~ .subscribe-alert-error {
    font-size: 10px;
    font-weight: var(--font-weight-primary-bold);
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    margin: 3vh auto 0;
    padding: 8px 15px;
    text-align: center;
    letter-spacing: 1.5px;
    text-transform: uppercase;
 }
 
 .subscribe-form.loading ~ .subscribe-alert-loading {
    color: var(--color-alert-orange);
 }
 
 .subscribe-form.error ~ .subscribe-alert-error {
    color: var(--color-alert-red);
 }
 
 /* Sucess info
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .subscribe-form.success,
 .subscribe-form ~ .subscribe-success {
    display: none;
 }
 
 .subscribe-form.success ~ .subscribe-success {
    display: inline-block;
 }
 
 /* Question about the account
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .subscribe-form.loading + small,
 .subscribe-form.success + small,
 .subscribe-form.error + small {
    display: none;
 }
 
 .subscribe-form + small {
    font-size: 14px;
    display: block;
    margin-top: 3vh;
 }
 
 .subscribe-form + small a {
    font-weight: var(--font-weight-primary-bold);
 }
 
