 /* --------------------------------------------------------------------------
    15.Search
    -------------------------------------------------------------------------- */
 .search-opened .global-wrap {
    overflow-x: hidden;
    overflow-y: scroll;
 }
 
 /* Fixes flickering when page load */
 .section-search:not(.is-load) {
    display: none;
    transition-duration: 0;
 }
 
 .search-content {
    margin: 140px auto 6vh;
 }
 
 .section-search,
 .search-image {
    transition: transform .75s ease-in-out;
    transform: translateX(100%);
 }
 
 .search-opened .search-image,
 .search-opened .section-search {
    transform: translateX(0);
 }
 
 /* Icons
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-open {
    display: inline-block;
    list-style: none;
    text-decoration: none;
 }
 
 .search-open svg {
    width: 17px;
    height: 17px;
    margin: 0 0 -4px 5px;
    list-style: none;
 }
 
 .search-close {
    position: absolute;
    z-index: 99999;
    top: 40px;
    right: 40px;
 }
 
 .search-close svg {
    width: 37px;
    height: 37px;
    fill: var(--color-elements-two);
 }
 
 .search-featured svg {
    width: 13px;
    height: 13px;
 }
 
 .search-open svg,
 .search-featured svg {
    fill: var(--color-font);
 }
 
 .search-open svg,
 .search-close svg {
    cursor: pointer;
    transition: all .2s ease-out;
 }
 
 .search-open svg {
    fill: var(--color-search-icon);
 }
 
 .search-close svg {
    fill: var(--color-elements-two);
 }
 
 .search-open svg:hover,
 .search-close svg:hover {
    fill: var(--ghost-accent-color);
 }
 
 /* Header - Search text hidden
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-open span {
    display: none;
 }
 
 /* Placeholder
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-input::-webkit-input-placeholder {
    color: var(--color-elements-two);
 }
 
 .search-input::-ms-input-placeholder {
    color: var(--color-elements-two);
 }
 
 .search-input::-moz-placeholder {
    opacity: 1;
    color: var(--color-elements-two);
 }
 
 .search-input::placeholder {
    color: var(--color-elements-two);
 }
 
 .search-input:focus {
    border-bottom-color: var(--ghost-accent-color);
 }
 
 /* Form - Search page
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-input {
    font-size: 30px;
    font-weight: var(--font-weight-primary-bold);
    display: block;
    flex: 0 0 100%;
    width: 100%;
    min-width: 100px;
    height: 62px;
    padding: 0;
    transition: border-color .3s ease;
    color: var(--color-font);
    border-width: 0 0 7px 0;
    border-style: solid;
    border-color: var(--color-elements-two);
    border-radius: 0;
    outline: 0;
    background-color: transparent;
 }
 
 /* Meta
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-meta {
    font-size: 10px;
    font-weight: var(--font-weight-primary-bold);
    display: block;
    width: 100%;
    margin: 0 auto;
    padding: 10px 0;
    text-align: left;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    color: var(--color-font);
 }
 
 .search-meta .hide {
    display: none;
 }
 
 /* Results
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-results h4 {
    font-size: 25px;
    position: relative;
    margin: 45px 0;
    padding: 0;
 }
 
 .search-results .search-date {
    font-size: 14px;
    font-weight: var(--font-weight-primary-regular);
    position: absolute;
    top: -18px;
    left: 0;
    width: 280px;
 }
 
 .search-results .search-featured {
    font-size: 14px;
    font-weight: var(--font-weight-primary-regular);
    position: absolute;
    top: -17px;
    left: -20px;
 }
 
 /* Suggestion
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
 .search-suggestion {
    width: 100%;
    margin-top: 8vh;
 }
 
 .search-suggestion h3 {
    font-size: 30px;
    width: 87%;
    margin-bottom: 20px;
 }
 
 .search-suggestion a {
    font-size: 11px;
    font-weight: var(--font-weight-primary-bold);
    display: inline-block;
    margin: 8px 5px 1px 0;
    padding: 5px 12px;
    letter-spacing: 1.7px;
    text-transform: uppercase;
    border-radius: 20px;
 }
 
 .search-suggestion-tags,
 .search-suggestion-authors {
    flex: 1 0 250px;
    margin: 2vh 0;
 }
 
 .search-suggestion-tags {
    margin-right: 50px;
 }
 
 /* RWD — Search
    –––––––––––––––––––––––––––––––––––––––––––––––––––– */
